* ::-webkit-scrollbar {
  width: 0;
}

* ::-webkit-scrollbar-thumb {
  background: transparent;
}

body {
  overflow: hidden;
  background: #2b2b2b;
}

a {
  text-decoration: none;
  color: inherit;
}

*[contenteditable],
input {
  user-select: text;
  -webkit-user-select: text;
}

* {
  box-sizing: border-box;
  font-weight: 300;
  cursor: default;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
}

.font-extrabold {
  font-weight: 800;
}

.font-bold {
  font-weight: 700;
}

.font-medium {
  font-weight: 500;
}

.font-normal {
  font-weight: 300;
}

.font-light {
  font-weight: 200;
}

.OptimisticDisplay {
  *:not(i) {
    font-family: Optimistic Display, -apple-system, ui-sans-serif, system-ui,
      BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans,
      sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol,
      Noto Color Emoji;
  }
}

.System {
  *:not(i) {
    font-family: -apple-system, ui-sans-serif, system-ui,
      BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans,
      sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol,
      Noto Color Emoji;
  }
}

.Desktop {
  overflow: hidden;
  width: 100%;
  height: 100vh;

  &.poweroff * {
    display: none;
    cursor: none !important;
  }
  &.blackscr * {
    display: none;
    cursor: default !important;
  }
  &.sleep {
    background-color: #000 !important;
    * {
      display: none;
      cursor: none !important;
    }
  }
  &.cursorLoad * {
    display: none;
    cursor: wait !important;
  }
  &.isBold * {
    font-weight: 700;
  }

  .DesktopBody {
    display: none;
    position: absolute;
    overflow: hidden;
    height: calc(100vh - 105px);
    width: 100vw;

    &.active {
      display: block;
    }
  }

  &::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    opacity: 0;
    display: block;
    height: 100%;
    width: 100%;
    z-index: 99999999999;
    background: #ffbf0049;
    pointer-events: none;
    transition: all 2s ease;
  }

  &.NightShiftEnabled::before {
    opacity: 0.8;
  }

  &.capture::before {
    animation: capture ease 2s;
  }

  @keyframes capture {
    0% {
      background-color: #fff;
      opacity: 1;
    }
    100% {
      background-color: transparent;
      opacity: 0;
    }
  }
}

.error {
  font-size: 15px;
  padding: 50px 25px;
  color: #fff;
  background: #000;
  margin: 0;
  width: 100%;
  height: 100vh;
  position: absolute;
  z-index: 1000000000000;
}

#brightness {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000000000000;
  background: #000;
  opacity: 0;
  pointer-events: none;
}
