.TerminalWindow {
    padding: 15px;
    color: #fff;
    background: #000000;
    margin: 0;
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index:1000000000000;
    overflow: auto;
    display: none;

    &.active {
        display: block;
    }

    &.cursorLoad {
        cursor: wait;
    }

    pre {
        font-size: 12px;
        font-family: 'Cascadia Code';
        margin: 0;
        width: fit-content;
    }

    .BootSplash {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        cursor: none!important;

        p {
            font-size: 80px;
            animation: blink 4s infinite;
            pointer-events: none;

            @keyframes blink {
                0% {
                    color: #fff;
                }
                50% {
                    color: #cfcfcf;
                }
                100% {
                    color: #fff;
                }
            }
        }
    }
}